/* Search */
.search-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.search-form {
  width: 100%;

}

.search-field-container {
  width: 100%;
  margin: 16px 0 16px 24px !important;
  max-width: 400px;
  display: inline-flex;
}

.search-field {
  width: 100%;
}

.search-clear {
  position: relative;
  left: -26px;
  top: 19px;
}

.search-button {
  height: 40px;
  width: 180px;
}

/* Results */
.result-controls {
  margin: 16px;
  display: flex;
  justify-content: space-between;
}

.filter-select {
  height: 32px;
}

.filter-label {
  color: black !important;
  margin-right: 12px;
}

.results-list {
  margin: 16px 8px 16px 16px;
}

.load-more-button {
  margin-top: 8px !important;
  width: 100%;
  height: 40px;
}

/* General */
.hidden {
  opacity: 0;
}

.rotate {
  animation: rotation 1s infinite linear;
}

.flex-center {
  display: flex;
  align-items: center;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}